.slick-slide{
  height:auto;
  outline:none
}

.slick-next,.slick-prev{
  width:40px;
  height:40px;
  line-height:40px;
  text-align:center;
  background:#000;
  border-radius:0;
  top:calc(50% - 40px);
  z-index:1;
  transform:translateX(0px)
}

.slick-next:hover,.slick-next:focus,.slick-prev:hover,.slick-prev:focus{
  background:#000
}

.slick-prev{
  left:0
}

.slick-prev:hover{
  background:#000
}

.slick-next{
  right:0
}

.slick-next:hover{
  background:#000
}

.slick-next:before,.slick-next:after{
  left:20px
}

.slick-prev:before,.slick-prev:after{
  left:17px
}

.slick-prev:before{
  content:"";
  background:#fff;
  display:block;
  width:1px;
  height:14px;
  top:8px;
  position:absolute;
  transform:rotate(45deg)
}

.slick-prev:after{
  content:"";
  background:#fff;
  display:block;
  width:1px;
  height:14px;
  position:absolute;
  top:17px;
  transform:rotate(135deg)
}

.slick-next:before{
  content:"";
  background:#fff;
  display:block;
  width:1px;
  height:14px;
  top:8px;
  position:absolute;
  transform:rotate(-45deg)
}

.slick-next:after{
  content:"";
  background:#fff;
  display:block;
  width:1px;
  height:14px;
  position:absolute;
  top:17px;
  transform:rotate(45deg)
}

.slick-dotted.slick-slider{
  margin-bottom:0
}

.slick-dots{
  bottom:0;
  margin-top:20px;
  position:relative
}

.slick-dots li{
  width:7px;
  height:7px;
  margin:0 3px
}

.slick-dots li.slick-active{
  width:7px;
  height:7px
}

.slick-dots li button{
  width:7px;
  height:7px;
  padding:0
}

.slick-dots li.slick-active button:before{
  background:#ff5959;
  opacity:1;
  height:7px;
  width:7px
}

.slick-dots li button:before{
  content:"";
  background:#e3e3e3;
  opacity:1;
  border-radius:100%;
  height:7px;
  width:7px
}

.slick-dots li button:hover:before,.slick-dots li button:focus:before{
  opacity:1;
  background:#ff5959
}

