@media only screen and (max-width: 991px){
  .site-wrapper{
    width:100%;
    padding-left:15px;
    padding-right:15px
  }

  header.left{
    top:25px;
    float:none !important;
    transform:translateX(-380px)
  }

  header.left.open{
    transform:translateX(0)
  }

  header.left button.close{
    display:block
  }

  .mobile-header{
    display:block
  }

  main.content{
    margin-top:20px;
    float:none !important;
    width:100%
  }
}

@media only screen and (max-width: 767px){
  .hero .cta h1{
    font-size:48px;
    line-height:1.2
  }

  .hero .cta h1 .dot{
    display:none
  }

  .hero .cta p{
    font-size:15px;
    margin-bottom:25px !important
  }

  .hero{
    height:auto;
    margin-top:0;
    padding:60px 30px
  }

  .fact-item{
    margin:20px 0
  }

  .blog-item{
    margin:15px 0
  }
}

